@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700,900&display=swap);
:root{--gradient: linear-gradient(135deg, #72edf2 10%, #5151e5 100%)}*{box-sizing:border-box;line-height:1.25em}.clear{clear:both}body{margin:0;width:100%;height:100vh;font-family:"Montserrat", sans-serif;background-color:#343d4b;display:flex;align-items:center;justify-content:center}.container{border-radius:25px;box-shadow:0 0 70px -10px rgba(0,0,0,0.2);background-color:#222831;color:#ffffff;height:400px}.weather-side{position:relative;height:100%;border-radius:25px;background-image:url("https://images.unsplash.com/photo-1559963110-71b394e7494d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=675&q=80");width:300px;box-shadow:0 0 20px -10px rgba(0,0,0,0.2);transition:-webkit-transform 300ms ease;transition:transform 300ms ease;transition:transform 300ms ease, -webkit-transform 300ms ease;-webkit-transform:translateZ(0) scale(1.02) perspective(1000px);transform:translateZ(0) scale(1.02) perspective(1000px);float:left}.weather-side:hover{-webkit-transform:scale(1.1) perspective(1500px) rotateY(10deg);transform:scale(1.1) perspective(1500px) rotateY(10deg)}.weather-gradient{position:absolute;width:100%;height:100%;top:0;left:0;background-image:linear-gradient(135deg, #72edf2 10%, #5151e5 100%);background-image:var(--gradient);border-radius:25px;opacity:0.8}.date-container{position:absolute;top:25px;left:25px}.date-dayname{margin:0}.date-day{display:block}.location{display:inline-block;margin-top:10px}.location-icon{display:inline-block;height:0.8em;width:auto;margin-right:5px}.weather-container{position:absolute;bottom:25px;left:25px}.weather-icon.feather{height:60px;width:auto}.weather-temp{margin:0;font-weight:700;font-size:4em}.weather-desc{margin:0}.info-side{position:relative;float:left;height:100%;padding-top:25px}.today-info{padding:15px;margin:0 25px 25px 25px;border-radius:10px}.today-info>div:not(:last-child){margin:0 0 10px 0}.today-info>div .title{float:left;font-weight:700}.today-info>div .value{float:right}.week-list{list-style-type:none;padding:0;margin:10px 35px;box-shadow:0 0 50px -5px rgba(0,0,0,0.25);border-radius:10px}.week-list>li{float:left;padding:15px 10px;cursor:pointer;transition:200ms ease;border-radius:10px}.week-list>li:hover{-webkit-transform:scale(1.1);transform:scale(1.1);background:#fff;color:#222831;box-shadow:0 0 40px -5px rgba(0,0,0,0.2)}.week-list>li.active{background:#fff;color:#222831;border-radius:10px}.week-list>li .day-name{display:block;margin:10px 0 0 0;text-align:center}.week-list>li .day-icon{display:block;height:30px;width:auto;margin:0 auto}.week-list>li .day-temp{display:block;text-align:center;margin:10px 0 0 0;font-weight:700}.location-container{padding:25px 35px}.location-button{outline:none;width:30%;box-sizing:border-box;border:none;border-radius:25px;padding:10px;font-family:"Montserrat", sans-serif;background-image:linear-gradient(135deg, #72edf2 10%, #5151e5 100%);background-image:var(--gradient);color:#ffffff;font-weight:700;box-shadow:0 0 30px -5px rgba(0,0,0,0.25);cursor:pointer;transition:-webkit-transform 200ms ease;transition:transform 200ms ease;transition:transform 200ms ease, -webkit-transform 200ms ease}.location-button:hover{-webkit-transform:scale(0.95);transform:scale(0.95)}.location-button .feather{height:1em;width:auto;margin-right:5px}.location-container{text-align:center}.location-container input{outline:none;width:50%;margin-right:10px;text-align:center;padding:0.2em 0em}.location_icon{margin-right:0.2em}.later-icon{font-size:30px}.Icon-Container{text-align:center}.form__group{position:relative}.form__field{font-family:inherit;width:100%;border:0;border-bottom:2px solid #9b9b9b;outline:0;font-size:1.3rem;color:#fff;padding:7px 0;background:transparent;transition:border-color 0.2s}.form__field::-webkit-input-placeholder{color:transparent}.form__field:-ms-input-placeholder{color:transparent}.form__field::placeholder{color:transparent}.form__field:-ms-input-placeholder ~ .form__label{font-size:1.3rem;cursor:text;top:20px}.form__field:placeholder-shown ~ .form__label{font-size:1.3rem;cursor:text;top:20px}.form__label{position:absolute;top:4%;left:14%;display:block;transition:0.2s;font-size:1rem;color:#9b9b9b}.form__field:focus{padding-bottom:6px;font-weight:700;border-width:3px;border-image:linear-gradient(to right, #11998e, #38ef7d);border-image-slice:1}.form__field:focus ~ .form__label{position:absolute;top:4%;left:14%;display:block;transition:0.2s;font-size:1rem;color:#11998e;font-weight:700}.form__field:required,.form__field:invalid{box-shadow:none}

